var router = function()
{
	var stack = new Map();
	var initialHash = true;

	// set all <a> events
	function updateDOMlist()
	{
		document.querySelectorAll("a").forEach(el => {
			el.onclick = anchorHandler;
		});

		if(initialHash)
		{
			initialHash = false;
			updateURL(window.location.pathname);
		}
	}

	// <a> click event
	function anchorHandler()
	{
		event.preventDefault();
		updateURL(event.target.pathname);
	}

	// change current url and emmiter him
	function updateURL(url)
	{
		var location = "#" + url;
		window.location.hash = location;

		emiter(url);
	}

	/*
	* @param {string} routeName
	*/
	function add(routeName, callback)
	{
		stack.set(routeName, callback);
	}

	/*
	* @param {string} stackName
	*/
	function emiter(stackName)
	{
		stack.get(stackName)();
	}

	window.onload = updateDOMlist;

	return {
		page: add,
		update: updateDOMlist,
	}
}();

export default router;